<template>
  <div>
    <div class="p-3">
      <h1>
        <router-link to="/index/home">
          <img class="indexlogo" src="../../assets/image/IndexLogo.svg" alt />
        </router-link>
      </h1>
    </div>
    <div class="my-3 row justify-content-center">
      <form class="col-md-6" @submit.prevent="payOrder">
        <table class="table">
          <thead>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right"></td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>

        <table class="table">
          <tbody>
            <tr>
              <th width="100">Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th></th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th></th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th></th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th></th>
              <td>
                <span v-if="!order.is_paid"></span>
                <span v-else class="text-success"></span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right">
          <router-link class="btn btn-outline-primary d-inline" to="/index/home"></router-link>
          <button class="btn btn-danger" v-if="order.is_paid === false"></button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      orderId: '',
      order: {
        user: {},
      },
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`;
      vm.isLoading = true;
      vm.$http.get(api).then((response) => {
        vm.order = response.data.order;
        vm.isLoading = false;
      });
    },
    payOrder() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`;
      vm.isLoading = true;
      vm.$http.post(api).then((response) => {
        if (response.data.success) {
          vm.getOrder();
          vm.isLoading = false;
        }
      });
    },
  },
  created() {
    this.orderId = this.$route.params.OrderId;
    this.getOrder();
  },
};
</script>
<style lang="scss" scoped>
</style>
